<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> A boolean Property specifying the visibility of the corridor.</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the array of Cartesian3 positions that define the centerline of the corridor. <strong>structure:[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A numeric Property specifying the distance between the edges of the corridor.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A numeric Property specifying the altitude of the corridor relative to the ellipsoid surface.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying what the height is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A numeric Property specifying the altitude of the corridor's extruded face relative to the ellipsoid surface.</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying what the extrudedHeight is relative to.  <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A CornerType Property specifying the style of the corners. <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td>RADIANS_PER_DEGREE</td>
<td><code v-pre>optional</code> A numeric Property specifying the distance between each latitude and longitude.</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> A boolean Property specifying whether the corridor is filled with the provided material.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> A Property specifying the material used to fill the corridor.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> A boolean Property specifying whether the corridor is outlined.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'black'</code></td>
<td><code v-pre>optional</code> A Property specifying the Color of the outline.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> A numeric Property specifying the width of the outline.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> An enum Property specifying whether the corridor casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying at what distance from the camera that this corridor will be displayed. <strong>structure:{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> An enum Property specifying whether this corridor will classify terrain, 3D Tiles, or both when on the ground. <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2, NUMBER_OF_CLASSIFICATION_TYPES: 3</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the zIndex of the corridor, used for ordering. Only has an effect if height and extrudedHeight are undefined, and if the corridor is static.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CorridorGraphics.html">CorridorGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',

          corridor1: {},
          name1: 'Red corridor on surface with rounded corners',
          positions1: [{ lng: 100.0, lat: 40.0 }, { lng: 105.0, lat: 40.0 }, { lng: 105.0, lat: 35.0 }],
          material1: {},

          corridor2: {},
          name2: 'Green corridor at height with mitered corners and outline',
          positions2: [{ lng: 90.0, lat: 40.0 }, { lng: 95.0, lat: 40.0 }, { lng: 95.0, lat: 35.0 }],
          cornerType2: 0,

          corridor3: {},
          name3: 'Blue extruded corridor with beveled corners and outline',
          positions3: [{ lng: 80.0, lat: 40.0 }, { lng: 85.0, lat: 40.0 }, { lng: 85.0, lat: 35.0 }],
          cornerType3: 0,
          material3: {}
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise, this.$refs.entity3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          this.material1 = Cesium.Color.RED.withAlpha(0.5)

          this.cornerType2 = Cesium.CornerType.MITERED

          this.cornerType3 = Cesium.CornerType.BEVELED
          this.material3 =  Cesium.Color.BLUE.withAlpha(0.5)
        }
      }
    }
  </script>